.post-list-item {
    display: flex;
    flex-flow: row nowrap;
    border-top: 4px solid $brand-color-light;
    padding: 25px 0;
}

.post-list-item a {
    color: $text-color;
}

.post-list-item a:visited {
    color: $text-color;
}

.post-list .post-content {
    padding-left: 1em;
    width: 73%;
}

.post-list .post-content h3 {
    margin-top: 0;
}

.post-content img {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.post-content video {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.post-content figcaption {
    margin-top: 0.5em;
    display: block;
    text-align: center;
    font-size: 0.9em;
    color: $border-color-01;
}

.post-list li {
    margin: 0;
}

.site-header {
    border-top: 1px solid $brand-color-dark;
    border-bottom: 3px solid $brand-color-dark;
}

th {
    color: $brand-color;
}

.site-footer {
    border-top: 1px solid $brand-color-dark;
}

ul.subscribe-links {
    margin: 0;
    list-style-type: none;
}

.footer-col {
    color: $site-title-color;
}

.post-navigation {
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    width: 100%;
    overflow: hidden;
}

.post-navigation a {
    margin: 1em 0;
    flex: 0 1 auto;
}

.post-navigation .next {
    text-align: left;
    margin-left: auto;
}

.post-navigation .prev {
    text-align: right;
}

blockquote {
    color: $border-color-01;
    font-style: normal;
}
