// Light mode
// ----------

$lm-brand-color: $color-pine-dawn !default;
$lm-brand-color-light: $color-highlight-mid-dawn !default;
$lm-brand-color-dark: $color-rose-dawn !default;

$lm-site-title-color: $color-pine-dawn !default;

$lm-text-color: $color-text-dawn !default;
$lm-background-color: $color-base-dawn !default;
$lm-code-background-color: $color-surface-dawn !default;

$lm-link-base-color: $color-rose-dawn !default;
$lm-link-visited-color: $color-rose-dawn !default;
$lm-link-hover-color: $color-rose-dawn !default;

$lm-border-color-01: $color-subtle-dawn !default;
$lm-border-color-02: $color-pine-dawn !default;
$lm-border-color-03: $color-gold-dawn !default;

$lm-table-text-color: $color-text-dawn !default;
$lm-table-zebra-color: $color-highlight-low-dawn !default;
$lm-table-header-bg-color: $color-highlight-mid-dawn !default;
$lm-table-header-border: $color-muted-dawn !default;
$lm-table-border-color: $color-muted-dawn !default;

// Syntax highlighting styles should be adjusted appropriately for every "skin"
// ----------------------------------------------------------------------------

@mixin lm-highlight {
    .highlight {
        .c {
            color: $color-muted-dawn;
            font-style: italic;
        } // Comment
        .err {
            color: $color-love-dawn;
        } // Error
        .k {
            color: $color-subtle-dawn;
            font-weight: bold;
        } // Keyword
        .o {
            color: $color-subtle-dawn;
            font-weight: bold;
        } // Operator
        .cm {
            color: $color-muted-dawn;
            font-style: italic;
        } // Comment.Multiline
        .cp {
            color: $color-muted-dawn;
            font-weight: bold;
        } // Comment.Preproc
        .c1 {
            color: $color-muted-dawn;
            font-style: italic;
        } // Comment.Single
        .cs {
            color: $color-muted-dawn;
            font-weight: bold;
            font-style: italic;
        } // Comment.Special
        .gd {
            color: $color-love-dawn;
            background-color: $color-surface-dawn;
        } // Generic.Deleted
        .gd .x {
            color: $color-love-dawn;
            background-color: $color-overlay-dawn;
        } // Generic.Deleted.Specific
        .ge {
            font-style: italic;
        } // Generic.Emph
        .gr {
            color: $color-love-dawn;
        } // Generic.Error
        .gh {
            color: $color-muted-dawn;
        } // Generic.Heading
        .gi {
            color: $color-foam-dawn;
            background-color: $color-surface-dawn;
        } // Generic.Inserted
        .gi .x {
            color: $color-foam-dawn;
            background-color: $color-overlay-dawn;
        } // Generic.Inserted.Specific
        .go {
            color: $color-text-dawn;
        } // Generic.Output
        .gp {
            color: $color-text-dawn;
        } // Generic.Prompt
        .gs {
            font-weight: bold;
        } // Generic.Strong
        .gu {
            color: $color-muted-dawn;
        } // Generic.Subheading
        .gt {
            color: $color-love-dawn;
        } // Generic.Traceback
        .kc {
            font-weight: bold;
        } // Keyword.Constant
        .kd {
            font-weight: bold;
        } // Keyword.Declaration
        .kp {
            font-weight: bold;
        } // Keyword.Pseudo
        .kr {
            font-weight: bold;
        } // Keyword.Reserved
        .kt {
            color: #458;
            font-weight: bold;
        } // Keyword.Type
        .m {
            color: #099;
        } // Literal.Number
        .s {
            color: #d14;
        } // Literal.String
        .na {
            color: #008080;
        } // Name.Attribute
        .nb {
            color: #0086b3;
        } // Name.Builtin
        .nc {
            color: #458;
            font-weight: bold;
        } // Name.Class
        .no {
            color: #008080;
        } // Name.Constant
        .ni {
            color: #800080;
        } // Name.Entity
        .ne {
            color: #900;
            font-weight: bold;
        } // Name.Exception
        .nf {
            color: #900;
            font-weight: bold;
        } // Name.Function
        .nn {
            color: #555;
        } // Name.Namespace
        .nt {
            color: #000080;
        } // Name.Tag
        .nv {
            color: #008080;
        } // Name.Variable
        .ow {
            font-weight: bold;
        } // Operator.Word
        .w {
            color: #bbb;
        } // Text.Whitespace
        .mf {
            color: #099;
        } // Literal.Number.Float
        .mh {
            color: #099;
        } // Literal.Number.Hex
        .mi {
            color: #099;
        } // Literal.Number.Integer
        .mo {
            color: #099;
        } // Literal.Number.Oct
        .sb {
            color: #d14;
        } // Literal.String.Backtick
        .sc {
            color: #d14;
        } // Literal.String.Char
        .sd {
            color: #d14;
        } // Literal.String.Doc
        .s2 {
            color: #d14;
        } // Literal.String.Double
        .se {
            color: #d14;
        } // Literal.String.Escape
        .sh {
            color: #d14;
        } // Literal.String.Heredoc
        .si {
            color: #d14;
        } // Literal.String.Interpol
        .sx {
            color: #d14;
        } // Literal.String.Other
        .sr {
            color: #009926;
        } // Literal.String.Regex
        .s1 {
            color: #d14;
        } // Literal.String.Single
        .ss {
            color: #990073;
        } // Literal.String.Symbol
        .bp {
            color: $color-muted-dawn;
        } // Name.Builtin.Pseudo
        .vc {
            color: #008080;
        } // Name.Variable.Class
        .vg {
            color: #008080;
        } // Name.Variable.Global
        .vi {
            color: #008080;
        } // Name.Variable.Instance
        .il {
            color: #099;
        } // Literal.Number.Integer.Long
    }
}

